* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 1em 5em;
}

@media only screen and (max-width: 768px) {
  
  body {
    padding: 1em 2.5em;
  }

}

/* ================================ GLOBAL ================================ */

@-webkit-keyframes fadein {
  from { opacity: 0 }
  to { opacity: 1 }
}

@keyframes fadein {
  from { opacity: 0 }
  to { opacity: 1 }
}

@-webkit-keyframes flashfade_half {
  0% { opacity: 0 }
  50% { opacity: 0.5 }
  100% { opacity: 0 }
}

@keyframes flashfade_half {
  0% { opacity: 0 }
  50% { opacity: 0.5 }
  100% { opacity: 0 }
}
html {
  background-color: black;
  margin: 0;
  padding: 0;
}
.banner {
  border-radius: 5px;
  display: flex;
  margin-top: -1em;
  margin-bottom: 1.5em;
  padding: 0.75em;

  -webkit-animation: fadein 0.5s;

          animation: fadein 0.5s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.banner-text {
  font-weight: bold;
}
.footer {
  border-radius: 5px;
  display: flex;
  margin-top: 2em;

  -webkit-animation: fadein 0.5s;

          animation: fadein 0.5s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.footer-text {
  color: #FFFFFF50;
  font-size: 0.75em;
  font-weight: bold;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}

.footer-text:hover {
  color: white;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}
.grid {
  display: flex;
  flex-wrap: wrap;
}
.header {
  display: flex;
  padding-bottom: 1.5em;
}

.header-logo {
  flex-grow: 1;
}
* {
  color: white;
}

.heading {
  font-size: 36px;
  font-weight: bold;
  margin: 0;

  -webkit-animation: fadein 1s;

          animation: fadein 1s;
}

.subheading {
  font-size: 18px;
  margin: 0;
  
  -webkit-animation: fadein 2s;
  
          animation: fadein 2s;
}

.text-container {
  padding: 1em 0;
}
a {
  color: white;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.text {
  background-color: black;
  color: white;
  font-weight: bold;
  position: absolute;
}

.primary {
  font-size: 1.5em;
  padding: 0.75em;
  top: 15%;
}

.title {
  font-size: 1em;
  margin: 0.5em 0;
  padding: 0 1em;
  top: 2%;
}

@media only screen and (max-width: 1280px) {

  a {
    text-decoration: underline;
  }

}

@media only screen and (min-width: 720px) and (max-width: 960px) {

  .primary {
    font-size: 2vw;
  }
  
  .title {
    font-size: 1.5vw;
  }

}

@media only screen and (min-width: 540px) and (max-width: 720px) {

  .primary {
    font-size: 2.5vw;
  }
  
  .title {
    font-size: 2vw;
  }

}

@media only screen and (min-width: 480px) and (max-width: 540px) {

  .primary {
    font-size: 2vw;
  }
  
  .title {
    font-size: 1.5vw;
  }

}
.tile {
  background-color: black;
  position: relative;
  overflow: hidden;
}
/* These rules actually apply to the .cover class in TileOverlay.css */
/* i.e. the rules affect all .cover elements inside .slides */

.slide .cover {
  top: 0;
  opacity: 1;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.slide2 .cover {
  top: -100%;
  opacity: 0;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

/* Only enable hover on desktop */
@media only screen and (min-width: 1280px) {

  .slide:hover .cover {
    top: -100%;
    opacity: 0;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }

}
.cover {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-size: cover;
  position: absolute;
}

.ic_arrowup {
  position: absolute;
  left: 46%;
  top: 92%;
  width: 8%;
  opacity: 0;
}

@media only screen and (max-width: 1280px) {

  .ic_arrowup {
    -webkit-animation: flashfade_half 3s 3s infinite;
            animation: flashfade_half 3s 3s infinite;
  }

}
